import React from 'react';
import './style.css';

const Teoria = () => {
    return (
        <div className="teoria-container">
            <h1 className="text-center">Teora del Movimiento de un Helicptero</h1>
            <p className="lead">
                Para comprender el movimiento de un helicptero, es importante conocer las ecuaciones que describen su movimiento longitudinal y de balanceo. Estas ecuaciones son fundamentales para entender cmo el helicptero se desplaza y se equilibra en el aire.
            </p>
            <h2 className="text-left">Ecuaciones del Movimiento Longitudinal</h2>
            <p>
                El movimiento longitudinal de un helicptero se describe mediante la siguiente ecuacin diferencial:
                <br />
                <strong>m (dx/dt) = Fp - ce (dx/dt)</strong>
                <br />
                Donde:
                <br />
                - <em>m</em> es la masa del helicptero.
                <br />
                - <em>Fp</em> es la fuerza del propulsor.
                <br />
                - <em>ce</em> es el coeficiente de resistencia.
            </p>
            <h2 className="text-left">Ecuaciones del Movimiento de Balanceo</h2>
            <p>
                El movimiento de balanceo de un helicptero se describe mediante la siguiente ecuacin diferencial:
                <br />
                <strong>Ix (d/dt) = d (Fu - Fb) - ct (d/dt)</strong>
                <br />
                Donde:
                <br />
                - <em>Ix</em> es el momento de inercia.
                <br />
                - <em>d</em> es la distancia del centro de masas al eje de balanceo.
                <br />
                - <em>ct</em> es el coeficiente de torsin.
                <br />
                - <em>Fu</em> es la fuerza de sustentacin.
                <br />
                - <em>Fb</em> es la fuerza de balanceo.
            </p>
            <h2 className="text-center">Video explicativo ecuaciones diferenciales del movimiento</h2>
            <div className="d-flex justify-content-center mb-4">
                <iframe 
                    title="Video de YouTube"
                    width="560" 
                    height="315" 
                    src="https://www.youtube.com/embed/xbGj5iuAJHw" 
                    frameBorder="0" 
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                    allowFullScreen
                    style={{ maxWidth: '100%' }}
                ></iframe>
            </div>
            <h2 className="text-center">Artculos de Inters</h2>
            <ul>
                <li>
                    <strong>Movimiento longitudinal: Dinmica de un vehculo en una pendiente</strong>
                    <p>Este artculo explora la dinmica de un vehculo que se mueve en una pendiente, considerando factores como la resistencia del aire, la friccin y la inclinacin de la pendiente. Se utilizan ecuaciones diferenciales para modelar el movimiento del vehculo y se analizan las condiciones para que el vehculo suba, baje o se mantenga en reposo.</p>
                    <a href="https://www.researchgate.net/publication/277977378_Modelling_and_validation_of_the_vehicle_longitudinal_model" target="_blank" rel="noopener noreferrer">Leer ms</a>
                </li>
                <li>
                    <strong>Movimiento de cabeceo: Anlisis de la estabilidad de un avin</strong>
                    <p>Este artculo analiza la estabilidad del movimiento de cabeceo de un avin, es decir, su capacidad para mantener una actitud horizontal durante el vuelo. Se utilizan ecuaciones diferenciales para modelar el movimiento del avin y se estudian los factores que influyen en su estabilidad, como la distribucin de masas, la aerodinmica y el piloto automtico.</p>
                    <a href="https://www.researchgate.net/figura/Lift-drag-ratio-as-a-function-of-lift-coefficient-estimated-by-aerodynamic-analysis-and_fig7_262185433" target="_blank" rel="noopener noreferrer">Leer ms</a>
                </li>
                <li>
                    <strong>Runge-Kutta: Un mtodo numrico para resolver ecuaciones diferenciales</strong>
                    <p>Este artculo presenta el mtodo de Runge-Kutta, un mtodo numrico ampliamente utilizado para resolver ecuaciones diferenciales ordinarias. Se explica el algoritmo de Runge-Kutta en detalle y se muestran ejemplos de su aplicacin.</p>
                    <a href="https://support.khanacademy.org/hc/en-us/community/posts/15295294509069-Numerical-Analysis-Methods-for-solving-partial-differential-equations" target="_blank" rel="noopener noreferrer">Leer ms</a> | <a href="https://www.youtube.com/watch?v=-1aNhFO1TcQ" target="_blank" rel="noopener noreferrer">Ver video</a>
                </li>
            </ul>
        </div>
    );
}

export default Teoria;