import React, { useState, useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';
import numeric from 'numeric';
import helicoptero from './imgs/helicoptero.png';
import './Simulador.css';

const Simulador = () => {
  const [x0, setX0] = useState('');
  const [v0, setV0] = useState('');
  const [theta0, setTheta0] = useState('');
  const [omega0, setOmega0] = useState('');

  const longitudinalChartRef = useRef(null);
  const pitchChartRef = useRef(null);

  const solve = () => {
    const x0Value = parseFloat(x0);
    const v0Value = parseFloat(v0);
    const theta0Value = parseFloat(theta0);
    const omega0Value = parseFloat(omega0);

    const t = numeric.linspace(0, 50, 500);

    const longMotion = (t, y) => {
      const [x, v] = y;
      const m = 1; // Masa del helicptero
      const ce = 1 / 3000; // Coeficiente de resistencia
      const Fp = 2 / 3; // Fuerza del propulsor
      return [v, (Fp - ce * v * v) / m];
    };

    const pitchMotion = (t, y) => {
      const [theta, omega] = y;
      const Ix = 1; // Momento de inercia
      const d = 1; // Distancia del centro de masas al eje de balanceo
      const ct = 0.1; // Coeficiente de torsin
      const Fu = 1; // Fuerza de sustentacin
      const Fb = 1; // Fuerza de balanceo
      return [omega, (d * (Fu - Fb) - ct * omega) / Ix];
    };

    const resultsLong = solveODE(longMotion, [x0Value, v0Value], t);
    const resultsPitch = solveODE(pitchMotion, [theta0Value, omega0Value], t);

    updateChart('longitudinalChart', t, resultsLong, 'Posicin', 'Velocidad', longitudinalChartRef.current);
    updateChart('pitchChart', t, resultsPitch, 'ngulo', 'Velocidad Angular', pitchChartRef.current);
  };

  const solveODE = (f, y0, t) => {
    const dt = t[1] - t[0];
    let y = [y0];
    for (let i = 1; i < t.length; i++) {
      const ti = t[i - 1];
      const yi = y[i - 1];
      const k1 = numeric.mul(dt, f(ti, yi));
      const k2 = numeric.mul(dt, f(ti + dt / 2, numeric.add(yi, numeric.div(k1, 2))));
      const k3 = numeric.mul(dt, f(ti + dt / 2, numeric.add(yi, numeric.div(k2, 2))));
      const k4 = numeric.mul(dt, f(ti + dt, numeric.add(yi, k3)));
      const yi_next = numeric.add(yi, numeric.div(numeric.add(numeric.add(k1, numeric.mul(2, k2)), numeric.add(numeric.mul(2, k3), k4)), 6));
      y.push(yi_next);
    }
    return y;
  };

  const updateChart = (chartId, labels, data, label1, label2, chartRef) => {
    const ctx = document.getElementById(chartId).getContext('2d');
    if (chartRef) {
      chartRef.data.labels = labels;
      chartRef.data.datasets[0].data = data.map(y => y[0]);
      chartRef.data.datasets[1].data = data.map(y => y[1]);
      chartRef.update();
    } else {
      const newChart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: labels,
          datasets: [
            {
              label: label1,
              data: data.map(y => y[0]),
              borderColor: 'blue',
              fill: false,
            },
            {
              label: label2,
              data: data.map(y => y[1]),
              borderColor: 'green',
              fill: false,
            },
          ],
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          scales: {
            x: {
              title: {
                display: true,
                text: 'Tiempo',
              },
            },
            y: {
              title: {
                display: true,
                text: label1 === 'Posicin' ? 'Valor (Longitudinal)' : 'Valor (Cabeceo)',
              },
            },
          },
        },
      });
      if (chartId === 'longitudinalChart') {
        longitudinalChartRef.current = newChart;
      } else {
        pitchChartRef.current = newChart;
      }
    }
  };

  const clearData = () => {
    setX0('');
    setV0('');
    setTheta0('');
    setOmega0('');

    if (longitudinalChartRef.current) {
      longitudinalChartRef.current.destroy();
      longitudinalChartRef.current = null;
    }

    if (pitchChartRef.current) {
      pitchChartRef.current.destroy();
      pitchChartRef.current = null;
    }
  };

  return (
    <div className="app">
      <div className="content">
        <div className="equations">
          <h2>Movimiento Longitudinal</h2>
          <p>La ecuacin diferencial que describe el movimiento longitudinal de un helicptero es:</p>
          <p>m (dx/dt) = F - c (dx/dt)</p>
          <input
            id="x0"
            type="number"
            placeholder="Posicin Inicial (x0)"
            value={x0}
            onChange={(e) => setX0(e.target.value)}
          />
          <input
            id="v0"
            type="number"
            placeholder="Velocidad Inicial (v0)"
            value={v0}
            onChange={(e) => setV0(e.target.value)}
          />

          <h2>Movimiento de Balanceo</h2>
          <p>La ecuacin diferencial que describe el movimiento de balanceo de un helicptero es:</p>
          <p>I (d/dt) = d (F - F) - c (d/dt)</p>
          <input
            id="theta0"
            type="number"
            placeholder="ngulo Inicial (0)"
            value={theta0}
            onChange={(e) => setTheta0(e.target.value)}
          />
          <input
            id="omega0"
            type="number"
            placeholder="Velocidad Angular Inicial (0)"
            value={omega0}
            onChange={(e) => setOmega0(e.target.value)}
          />

          <button id="solveButton" onClick={solve}>
            Resolver
          </button>
          <button id="clearButton" onClick={clearData}>
            Limpiar Datos
          </button>
        </div>
        <div className="image">
            <img src={helicoptero} alt="Helicptero" />
        </div>
      </div>
      <div id="results">
        <div id="longitudinal-results">
          <h2>Resultados del Movimiento Longitudinal</h2>
          <canvas id="longitudinalChart"></canvas>
        </div>
        <div id="pitch-results">
          <h2>Resultados del Movimiento de Balanceo</h2>
          <canvas id="pitchChart"></canvas>
        </div>
      </div>
    </div>
  );
};

export default Simulador;