// Cards.js
import React from 'react';
import simulator from './imgs/helicoptero.png';
import info from './imgs/horspool.jpg';
import complexity from './imgs/pdf.jpg';
import './style.css';

const cardsData = [
  {
    title: "Simulador de Cdigo",
    description: "Herramienta interactiva para simular y graficar ecuaciones diferenciales presentes en un helicptero de dos grados de libertad.",
    imgSrc: simulator,
    component: 'simulador'
  },
  {
    title: "Teora de Ec.Dif",
    description: "Informacin detallada sobre las ecuaciones de movimiento longitudinal y balanceo,(Runge-Kutta) con un video explicativo.",
    imgSrc: info,
    component: 'teoria'
  },
  {
    title: "PDF Teorico",
    description: "PDF con trabajo sobre un un helicptero de dos grados de libertad y las ecuaciones diferenciales presentes en el.",
    imgSrc: complexity,
    component: 'complejidad'
  }
];

export default function Cards({ onSelect }) {
  return (
    <div className='container d-flex justify-content-center h-100 align-items-center'>
      <div className='row'>
        {cardsData.map((card, index) => (
          <div key={index} className='col-md-4 mt-0'>
            <div className="card text-center bg-dark animate__animated animate__fadeInUp" onClick={() => onSelect(card.component)}>
              <div className='overflow'>
                <img src={card.imgSrc} alt={`Imagen de ${card.title}`} className='card-img-top' style={{ width: '100%', marginTop: '-16px' }} />
              </div>
              <div className="card-body text-light">
                <h4 className="card-title">{card.title}</h4>
                <p className='card-text text-secondary'>{card.description}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}